import React from 'react';
import { Helmet } from 'react-helmet-async';
export default function SEO(props) {
  const title = props.title || 'OH EUN HO';
  const description = props.description || 'OH EUN HO';
  const keywords = props.keywords || 'OH EUN HO';
  const name = props.name || 'Huen Oh';
  const type = props.type || 'website';

  return (
    <Helmet>
      {/* Standard metadata tags */}
      <title>{title}</title>
      <meta name="author" content={name} />
      <meta name="description" content={description} />
      <meta name="keywords" content={keywords} />
      {/* End standard metadata tags */}
      {/* Facebook tags */}
      <meta property="og:type" content={type} />
      <meta property="og:title" content={title} />
      <meta property="og:site_name" content={title} />
      <meta property="og:description" content={description} />
      {/* <meta property="og:image" content={props.imgsrc} />
      <meta property="og:url" content={props.url} /> */}
      {/* End Facebook tags */}
      {/* Twitter tags */}
      <meta name="twitter:creator" content={name} />
      <meta name="twitter:card" content={type} />
      <meta name="twitter:title" content={title} />
      <meta name="twitter:description" content={description} />
      {/* <meta name="twitter:image" content={props.imgsrc} /> */}

      {/* <link rel="canonical" href={props.url} /> */}
      {/* End Twitter tags */}
    </Helmet>
  );
}
